.image-list{
    margin-top: 40px;
    display:grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0px 1px;
    grid-auto-rows: 10px;
}

.image-list img {
    width: 250px;
    padding-bottom: 5px;
}

/* RESPONSIVE */
@media screen and (max-width: 640px) {

    .image-list{
        margin-top: 120px;
        display:grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        grid-gap: 0px 1px;
        grid-auto-rows: 10px;
    }

    .image-list img {
        width: 95vw;
        padding-bottom: 5px;
        margin-left: 10px;
    }
}